@mixin heart {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.overun {
  .empty_matter {
  .empty_one {
    max-width: 140px;
    max-height: 140px;
    width: 110px;
    height: 110px;
    background-color: rgba(204, 204, 204, 0.267);
    @include heart();
  }
  .sofo{
    color: rgb(179, 175, 175);
  }
  .errored,
  .empty_oned {
    width: 100%;
    height: 100%;
    background-size: 100%;
    filter: opacity(40%);
    // background-image: url("./EC.png");
    @include heart();
  }
  .errored {
    background-image: url("./erroed.png") !important;
  }
  .onr {
    font-size: 15px;
  }
  .empty_text {
    color: rgba(5, 5, 5, 0.39) !important;
  }
}
